
import Webgl from '@/components/Webgl/Webgl.vue'
import { ref } from 'vue'

import mitt from '@/mitt/mitt'

export default {
  name: 'App',
  components: {
    Webgl,
  },
  setup() {
    const velocity = ref(0)
    const acceleration = ref(0)
    const drag = ref(0)

    mitt.on('velocity', v => {
      velocity.value = v
    })

    mitt.on('acceleration', v => {
      acceleration.value = v
    })

    mitt.on('drag', v => {
      drag.value = v
    })

    return {
      velocity,
      acceleration,
      drag,
    }
  },
}
